
.sliders{
    width: 600px;
}

.swiper-slide{
    background: #fff;
    padding: 40px;
    border-radius: 5px;
}

.slide__img-01 , .slide__img-02, .slide__img-03 img{
    width: 100%;
}

.slide__img-01{
    background: #eeeaff;
}

.slide__img-02{
    background: #d3cef2;
}

.slide__img-03{
    background: #8f579e;
}

.slide__img-01 , .slide__img-02, .slide__img-03 {
    border-radius: 0.8rem;
    margin-bottom: 30px;
}

.slide__item p{
    color: var(--heading-color);
    line-height: 28px;
}

.slide__item h4{
    color: var(--heading-color);
    margin-bottom: 10px;
}

.sliders h2{
    font-size: 2.5rem;
    margin-bottom: 50px;
    text-align: center;
}
    

